export function searchCountry(text) {
    const filterByText = (country) =>
        country.name.toLowerCase().includes(text.toLowerCase());
    const compareByText = (left, right) => {
        if (right.name.startsWith(text)) {
            return 1;
        }
        if (left.name.startsWith(text)) {
            return -1;
        }
        for (const word of right.name.split(" ")) {
            if (word.startsWith(text)) {
                return 1;
            }
        }
        for (const word of left.name.split(" ")) {
            if (word.startsWith(text)) {
                return -1;
            }
        }
        return left.name.localeCompare(right.name);
    }

    return countryList.filter(filterByText).sort(compareByText);
}

export const countriesWithShape = countryList.filter(country => country.shape != null);
export const countriesWithFlags = countryList.filter(country => country.flag != null);